import { Component, OnInit } from '@angular/core';
import { PassNodeInfoService } from '../shared/services/pass-node-info.service';
import { NodeInfo } from '../shared/models/node-info';
import { Subscription } from 'rxjs/Subscription';
import { OnDestroy } from '@angular/core/src/metadata/lifecycle_hooks';

@Component({
  selector: 'app-network',
  templateUrl: './network.component.html',
  styleUrls: ['./network.component.css'],
  providers: [ PassNodeInfoService ]
})
export class NetworkComponent implements OnInit, OnDestroy {
  nodeInfo: NodeInfo;
  nodeType: string;
  subscription: Subscription;

  constructor(
    private pNodeInfoService: PassNodeInfoService
  ) {
    this.nodeType = "forceGraph"
    this.nodeInfo = new NodeInfo();
    this.nodeInfo.name = 'network works!';
    this.subscription = this.pNodeInfoService.nodeInfoAnnounced.subscribe(
      nodeInfo => {
        this.nodeInfo = nodeInfo;
      }
    )
  }

  ngOnInit() {
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}



