import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { NodeInfo } from '../shared/models/node-info';
import { PassNodeInfoService } from '../shared/services/pass-node-info.service';
import { Subscription }   from 'rxjs/Subscription';


@Component({
  selector: 'app-tree',
  templateUrl: './tree.component.html',
  styleUrls: ['./tree.component.css'],
  providers: [ PassNodeInfoService ]
})
export class TreeComponent implements OnInit, AfterViewInit, OnDestroy {
  // two parameters for info panel
  nodeInfo: NodeInfo;
  nodeType: string;

  subscription: Subscription;

  constructor(private pNodeInfo: PassNodeInfoService){
    // initialize the nodeInfo for info panel
    this.nodeInfo = new NodeInfo();
    this.nodeInfo.name = " ";
    this.nodeType = 'tidyTree'

  	this.subscription = this.pNodeInfo.nodeInfoAnnounced.subscribe(
  		nodeInfo => {
  			this.nodeInfo = nodeInfo;
  		}
    )
  }

  ngOnInit(){}

  ngAfterViewInit(){}

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}



