import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppRoutingModule } from './app-routing/app-routing.module';
import { AppComponent } from './app.component';
import { TreeDataService } from './shared/services/tree-data.service';
import { TreeComponent } from './tree/tree.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { NetworkComponent } from './network/network.component';
import { InfoPanelComponent } from './shared/components/info-panel/info-panel.component';
import { TidyTreeComponent } from './shared/components/visual/tidy-tree/tidy-tree.component';
import { TreemapComponent } from './shared/components/visual/treemap/treemap.component';
import { ForceDirectedGraphComponent } from './shared/components/visual/force-directed-graph/force-directed-graph.component';
import { PassNodeInfoService } from './shared/services/pass-node-info.service';
import { GraphDataService } from './shared/services/graph-data.service';
import { CommunityDetectionService } from './shared/services/community-detection.service'


@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule
  ],
  declarations: [
    AppComponent,
    TreeComponent,
    DashboardComponent,
    NetworkComponent,
    InfoPanelComponent,
    TidyTreeComponent,
    TreemapComponent,
    ForceDirectedGraphComponent
  ],
  providers: [
    TreeDataService, 
    PassNodeInfoService,
    GraphDataService,
    CommunityDetectionService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



