import { Injectable } from '@angular/core';
import { Subject }    from 'rxjs/Subject';
import { NodeInfo } from '../models/node-info';

@Injectable()
export class PassNodeInfoService {

  constructor() { }

  // Observable sources
  nodeInfoAnnouncedSource = new Subject<NodeInfo>();

  // Observable streams
  nodeInfoAnnounced = this.nodeInfoAnnouncedSource.asObservable();

  // Service commands
  announceNodeInfo(nodeInfo: NodeInfo) {
  	this.nodeInfoAnnouncedSource.next(nodeInfo);
  }

}



