import { Component, OnInit } from '@angular/core';
import { AfterViewInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { GraphDataService } from '../../../services/graph-data.service';
import { CommunityDetectionService } from '../../../services/community-detection.service';
import * as D3 from 'd3';
import * as jl from 'jlouvain';

@Component({
  selector: 'app-force-directed-graph',
  templateUrl: './force-directed-graph.component.html',
  styleUrls: ['./force-directed-graph.component.css']
})

export class ForceDirectedGraphComponent implements OnInit, AfterViewInit {
  nodeDataForCommunityDetection: any = [];
  edgeDataForCommunityDetection: any = [];
  nodeDataForViz: any = {};
  edgeDataForViz: any = {};
  publicKeyToIndexKeyMap: any = {};
  egoNetworkActivated: boolean = false;
  linkedMatrix: any = {};
  maxDegree: number = 0;
  minDegree: number = 0;
  nodeSum: number = 0;
  edgeSum: number = 0;
  degreeSum: number = 0;
  averageDegree: number = 0;
  testDegreeArray = [];

  constructor(
    private graphDataService: GraphDataService,
    private communityDetectionService: CommunityDetectionService
  ) { }

  ngOnInit() { }

  initializeData(dataInput) {
    if(!dataInput){alert("no graph data input found.")};

    this.nodeDataForViz = dataInput.nodes;
    this.nodeSum = this.nodeDataForViz.length;
    this.nodeDataForViz.forEach((node, i) => {
      this.nodeDataForCommunityDetection.push(i);
      node["key"] = i;
      node["degree"] = 0;
      this.publicKeyToIndexKeyMap[node.nodeid] = i;
    });
  
    this.edgeDataForViz =  dataInput.channels;
    this.edgeSum = this.edgeDataForViz.length;
    let sourceNodeKey = "";
    let targetNodeKey = "";
    let sourceNodeExits: number;
    let targetNodeExits: number;
    // test the degree thredshold
    let degreeArray: number[] = [];
    this.edgeDataForViz.forEach((edge, i) => {
      sourceNodeKey = edge.source;
      targetNodeKey = edge.destination;
      sourceNodeExits = this.publicKeyToIndexKeyMap[sourceNodeKey];
      targetNodeExits = this.publicKeyToIndexKeyMap[targetNodeKey];
      if(sourceNodeExits && targetNodeExits){
        this.edgeDataForCommunityDetection.push(
          {
            source: sourceNodeExits,
            target: targetNodeExits,
            weight: 1
          }
        );
        // calculate the degree of related node
        let sourceDegree = (this.nodeDataForViz[sourceNodeExits]["degree"] += 1);
        let targetDegree = (this.nodeDataForViz[targetNodeExits]["degree"] += 1);
        if(sourceDegree > this.maxDegree) {
          this.maxDegree = sourceDegree;
        }
        if(sourceDegree < this.minDegree) {
          this.minDegree = sourceDegree;
        }
        if(targetDegree > this.maxDegree) {
          this.maxDegree = targetDegree;
        }
        if(targetDegree < this.minDegree) {
          this.minDegree = targetDegree;
        }

        // initialize the linkeMatrix
        this.linkedMatrix[sourceNodeExits + ',' + targetNodeExits] = 1;
        this.linkedMatrix[targetNodeExits + ',' + sourceNodeExits] = 1;
      };
    });
    this.nodeDataForViz.forEach((node)=>{
      this.degreeSum += node.degree;
      // test the degree thredshold
      degreeArray.push(node.degree/2);
    });
    this.degreeSum = this.degreeSum/2;
    this.averageDegree = Math.round(this.degreeSum / this.nodeSum);
    // test the degree thredshold
    degreeArray.sort((n1,n2)=> {return (n2 - n1)});
    this.testDegreeArray = degreeArray;
    // console.log("the degree array:", degreeArray);
    // console.log("the degree sum:",this.degreeSum);
    // download the file
    // this.downloadTheArray(degreeArray);

  }

  ngAfterViewInit() {
    this.graphDataService.getGraphDataTest()
      .then( data => {
        this.initializeData(data);
        this.renderForceDirectedGraph();
        // test
        console.log(this.testDegreeArray);
        let percent = 0.0;
        let sum = 0;
        let testIndex = 0;
        for(let i = 0; i < 603; i++){
          sum += this.testDegreeArray[i];
          percent = sum / (this.degreeSum);
          if(percent>=0.8) {
            testIndex = i;
            break;
          }
        }
        console.log(testIndex);
        // this.calculateTheCommunityPersentage();
      })
  };

  renderForceDirectedGraph() {
    let testMatrix = this.linkedMatrix;
    let maxD = this.maxDegree;
    let minD = this.minDegree;
    let degreeSize = D3.scaleLinear().domain([minD, maxD]).range([3,15]);
    let node_data = this.nodeDataForCommunityDetection;
    let edge_data = this.edgeDataForCommunityDetection;
    let community = this.communityDetectionService.run().nodes(node_data).edges(edge_data);
    // test
    let testComm = community();
    console.log("what we got here:", testComm);
    this.calculateTheCommunityPersentage(testComm);
    // layout parameter
    let width = 800;
    let height = 500;
    let original_node_data = this.nodeDataForViz;
    // use force simulation
    let simulation = D3.forceSimulation()
        .force("link", D3.forceLink())
        .force("charge", D3.forceManyBody().strength(-150).distanceMax(500))
        .force("center", D3.forceCenter(width/2, height/2));
    let svg = D3.select("#forceGraph").append("svg")
      .attr("width", width)
      .attr("height", height)
      .call(D3.zoom().on('zoom', zoomed));
    let container = svg.append('g');
    
    simulation.force("collide", D3.forceCollide((d:any)=> {return degreeSize(d.degree);}));

    let link = container.selectAll(".link")
        .data(edge_data)
      .enter().append("line")
        .attr("class", "link")
        .style("stroke-width", (d)=> {return 1;});

    let node = container.selectAll(".node")
        .data(original_node_data)
      .enter().append("circle")
        .attr("class", "node")
        .attr("r", (d:any) => { 
          return degreeSize(d.degree);
          // return 5;
        })
        .style("fill", '#a30500')
        .on("click", showGraphNodeInfo);
        // .call(D3.drag()
        //   .on("start", dragstarted)
        //   .on("drag", dragged)
        //   .on("end", dragended));

    simulation.nodes(original_node_data)
      .on("tick", function(){
        link.attr("x1", function(d:any) { return d.source.x; })
            .attr("y1", function(d:any) { return d.source.y; })
            .attr("x2", function(d:any) { return d.target.x; })
            .attr("y2", function(d:any) { return d.target.y; });
        node.attr("cx", function(d:any) { return d.x; })
            .attr("cy", function(d:any) { return d.y; });
      });

    simulation.force("link", D3.forceLink(edge_data).distance(20).strength(1));

    D3.select('#forceGraphCDButton').on('click', function(){
      //Communnity detection on click event
      // let community_assignment_result = community();
      // test
      console.log("the parameter:", testComm);
      let community_assignment_result = testComm;
      let node_ids = Object.keys(community_assignment_result);
      console.log('Resulting Community Data', community_assignment_result);
      let max_community_number = 0;
      node_ids.forEach(function(d:any){
        original_node_data[d].community = community_assignment_result[d];
        max_community_number = max_community_number < community_assignment_result[d] ? community_assignment_result[d]: max_community_number;
      });
      console.log(max_community_number);
      // let color = D3.scaleOrdinal(D3.schemeCategory20); //TODO color domain not specified
      // let color = D3.scaleLinear().domain([0,max_community_number]).range(D3.schemeCategory20);
      let color = D3.scaleSequential(D3.interpolateRgbBasis(D3.schemeCategory20c)).domain([0, max_community_number]);
      D3.selectAll('.node')
        .data(original_node_data)
        .style('fill', (d:any): any => { return color(d.community);});
    });
    D3.select('#forceGraphResetButton').on('click', function(){
      D3.selectAll('.node')
        .data(original_node_data)
        .style('fill', '#a30500');
    });

    function zoomed() {
        container.attr("transform", "translate(" + D3.event.transform.x + ", " + D3.event.transform.y + ") scale(" + D3.event.transform.k + ")");
    }

    function neighboring(a: any, b:any) {
      return testMatrix[a.index + ',' + b.index];
    }  

    function showGraphNodeInfo(d){
      D3.select("#graphNodeInfoAlias")
        .text('Node Alias: ' + d.alias);
      D3.select("#graphNodeInfoDegree")
        .text('Node Degree: ' + d.degree/2);
      this.testString = d.alias;
      if(!this.egoNetworkActivated){
        D3.selectAll('.link').style('stroke-opacity', function (l:any) {
          return l.target == d || l.source == d ? 1 : 0.1;
        });
        D3.selectAll('.node').style('opacity', function (n) {
          return neighboring(d, n) ? 1 : 0.1;
        });
        D3.select(this).style('opacity', 1);
        this.egoNetworkActivated = 1;
      } else {
        // Restore nodes and links to normal opacity.
        D3.selectAll('.link').style('stroke-opacity', '0.6');
        D3.selectAll('.node').style('opacity', '1');
        this.egoNetworkActivated = 0;
      }
    }

    function dragstarted(d) {
      if (!D3.event.active) simulation.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y;
    }
    
    function dragged(d) {
      d.fx = D3.event.x;
      d.fy = D3.event.y;
    }
    
    function dragended(d) {
      if (!D3.event.active) simulation.alphaTarget(0);
      d.fx = null;
      d.fy = null;
    }
  }

  downloadTheArray(data:any) {
    var fileContents = "";
    var filename = "hello.txt";
    var filetype = "text/plain";

    // intialize the content
    data.forEach((element, i) => {
      fileContents +=  i + " " + element + "\n"
    });

    var a = document.createElement("a");
    var dataURI = "data:" + filetype +
        ";base64," + btoa(fileContents);
    a.href = dataURI;
    a['download'] = filename;
    var e = document.createEvent("MouseEvents");
    // Use of deprecated function to satisfy TypeScript.
    e.initMouseEvent("click", true, false,
        document.defaultView, 0, 0, 0, 0, 0,
        false, false, false, false, 0, null);
    a.dispatchEvent(e);
    a.remove();
  }

  calculateTheCommunityPersentage(communityData: any) {
    // let community = this.communityDetectionService.run().nodes(this.nodeDataForCommunityDetection).edges(this.edgeDataForCommunityDetection);
    // let community_assignment_result = community();
    console.log("what we got here:", communityData);
    let node_ids = Object.keys(communityData);
    let differentCommArrays: number[][] = new Array(34);
    node_ids.forEach((i:any) => {
      differentCommArrays[communityData[i]] = differentCommArrays[communityData[i]]? differentCommArrays[communityData[i]] : new Array();
      differentCommArrays[communityData[i]].push(i);
    });
    console.log(differentCommArrays);
  }
}


