import { NgModule } from '@angular/core';
import { RouterModule, Routes }  from '@angular/router';

import { DashboardComponent } from '../dashboard/dashboard.component';
import { TreeComponent } from '../tree/tree.component';
import { NetworkComponent } from '../network/network.component';
import { TidyTreeComponent } from '../shared/components/visual/tidy-tree/tidy-tree.component';
import { TreemapComponent } from '../shared/components/visual/treemap/treemap.component';
import { ForceDirectedGraphComponent } from '../shared/components/visual/force-directed-graph/force-directed-graph.component';

const appRoutes: Routes = [
	{path: 'dashboard', component: DashboardComponent },
	{
    path: 'tree', 
    component: TreeComponent, 
    children:[
      {
        path: 'treemap',
        component: TreemapComponent
      },
      {
        // as default page, first to lauch the tidy tree
        path: '',
        component: TidyTreeComponent
      }
    ]
  },
	{
    path: 'network', 
    component: NetworkComponent,
    children: [
      {
        // force directed graph as default visual interpretation
        path: '',
        component: ForceDirectedGraphComponent
      }
    ]
  },
	{path: '', redirectTo: '/dashboard', pathMatch: 'full'}
]

@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



