import { Component, OnInit } from '@angular/core';
import * as D3 from 'd3';
import { AfterViewInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { PassNodeInfoService } from '../../../services/pass-node-info.service';
import { NodeInfo } from '../../../models/node-info';

@Component({
  selector: 'app-treemap',
  templateUrl: './treemap.component.html',
  styleUrls: ['./treemap.component.css']
})
export class TreemapComponent implements OnInit, AfterViewInit {
  data: any;
  treemapLayout: any;
  rootNode: any;

  constructor(
    private pNodeInfoService: PassNodeInfoService
  ) {
    // initialize the data
    this.data = {
      "name": "angular.core",
      "children": [
        {
          "name": "lifecycles",
          "children": [
            {
              "name": "OnInit",
              "value": 100
            },
            {
              "name": "OnChange",
              "value": 100
            },
            {
              "name": "AfterContentInit",
              "value": 300
            },
            {
              "name": "AfterContentChecked",
              "value": 200
            },
            {
              "name": "AfterViewInit",
              "value": 200
            },
            {
              "name": "OnDestroy",
              "value": 100
            }
          ]
        },
        {
          "name": "Component",
          "value": 500
        }
      ]
    }
  }

  ngOnInit() {
    this.treemapLayout = D3.treemap()
      .tile(D3.treemapBinary) // different strategies are available: treemapBinary/treemapSlice etc.
      .size([800, 500])
      .paddingOuter(16);
    
    this.rootNode = D3.hierarchy(this.data);

    this.rootNode.sum( d => {return 1}); // change this d.value to 1 to make count valueable

    this.treemapLayout(this.rootNode);
  }

  ngAfterViewInit(){
    let annouceNodeInfoSource = this.pNodeInfoService.nodeInfoAnnouncedSource;
    let nodes = D3.select('#treemap').select('svg g')
      .selectAll('g')
      .data(this.rootNode.descendants())
      .enter()
      .append('g')
      .attr('transform', (d:any) => {return 'translate(' + [d.x0, d.y0] + ')'});
    
    nodes
      .append('rect')
      .attr('width', (d:any) => {return d.x1 - d.x0})
      .attr('height', (d:any) => {return d.y1 - d.y0})
      .on('click', showNodeInfo);

    nodes
      .append('text')
      .attr('dx', 4)
      .attr('dy', 14)
      .text((d:any) => {return d.data.name});

    function showNodeInfo(node: any){
      let n: NodeInfo = new NodeInfo();
      n.name = node.data.name;
      annouceNodeInfoSource.next(n);
    }
  }
}



